.loginBlockMainDiv {
    display: block;
    margin-right: 455px;
}

.loginBlock {
    display: flex;
    align-items: center;
    height: min-content;
    gap: 5px;
}

.loginBlock a {
    text-decoration: none;
    color: black;
    width: min-content;
    padding-right: 15px;
}

.logoutButton {
    height: max-content;
    width: 70px;
}

.userPhotoBlock img {
    height: 32px;
    width: 32px;
    border-radius: 50px;
}

.topNavButton {
    display: flex;
    align-items: center;
    gap: 10px;
}

.userPanel {
    width: 10%;
    background-color: white;
    box-shadow: 0 20px 40px;
    position: absolute;
}
